import React from "react";
import Search1 from "../../Search/Search";
import { connect } from "react-redux";
import {
  BottomNavigation,
  AppBar,
  Toolbar,
  Typography,
  createMuiTheme,
  ThemeProvider,
  Slide,
  Button,
  BottomNavigationAction,
  IconButton,
} from "@material-ui/core";
import ProfileButton from "../profileButtonMenu";
import { Image, Visibility } from "semantic-ui-react";

import { BackTop } from "antd";
// const myWhite = "rgba(255,255,255,0.9)";
const myBlack = "rgba(0,0,0,0.5)";
const myTheme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(0,0,0,0.0)",
    },
    secondary: {
      main: myBlack,
    },
  },
});

const bottomTheme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(0,0,0,1)",
    },
  },
});

const ForMobile = (props) => {
  const [isTop, setDirection] = React.useState(true);
  const [topVisible, setTop] = React.useState(true);
  return (
    <Visibility
      onUpdate={(e, { calculations }) => {
        setTop(calculations.topVisible);
        setDirection(calculations.direction === "up");
      }}
    >
      <BackTop
        style={{ position: "fixed", left: "4%", bottom: "10%" }}
      ></BackTop>
      <ThemeProvider theme={myTheme}>
        <Slide in={true}>
          <AppBar color="primary" position="static" elevation={0}>
            <Toolbar style={{ height: 10 }}>
              <Button
                style={{ textTransform: "none" }}
                onClick={() => {
                  props.dispatch({
                    type: "Home",
                  });
                  props.history.push("/anonymity/Home");
                }}
              >
                <Image
                  src={require("../../../images/complainify.png")}
                  size="mini"
                ></Image>
                <Typography
                  variant="h6"
                  style={{ color: "black", marginLeft: "10%" }}
                >
                  {props.currentTab}
                </Typography>
              </Button>
              <div style={{ flexGrow: 1 }}></div>
              {props.currentTab !== "Messages" ? (
                <div
                  className="comingFromUpP5"
                  style={{ padding: 12, cursor: "pointer" }}
                  onClick={() => {
                    props.history.push("/anonymity/messages");
                    props.dispatch({ type: "Messages" });
                  }}
                >
                  <i
                    style={{ color: "grey" }}
                    className="far fa-paper-plane"
                  ></i>
                </div>
              ) : (
                <div
                  style={{ padding: 10, cursor: "pointer" }}
                  onClick={() => {
                    props.history.push("/anonymity/home");
                    props.dispatch({ type: "Home" });
                  }}
                  className="comingFromDownP5"
                >
                  <i style={{ color: "grey" }} className="fas fa-home"></i>
                </div>
              )}
              <Search1 history={props.history}></Search1>
              <ProfileButton {...props}></ProfileButton>
            </Toolbar>
          </AppBar>
        </Slide>
      </ThemeProvider>
      {props.currentTab !== "Messages" ? (
        <ThemeProvider theme={bottomTheme}>
          <BottomNavigation
            value={props.currentTab}
            onChange={(event, type) => {
              props.dispatch({
                type,
              });
              props.history.push("/anonymity/" + type);
            }}
            style={{
              position: "fixed",
              bottom: 0,
              zIndex: 100,
              width: "100%",
            }}
          >
            <BottomNavigationAction
              // label="Home"
              value={"Home"}
              label="Home"
              icon={
                <Image
                  size="mini"
                  style={{ width: 23, height: 23 }}
                  src="https://img.icons8.com/color/96/000000/home.png"
                />
              }
            ></BottomNavigationAction>
            <BottomNavigationAction
              // label="Home"
              value={"Person"}
              label="Profile"
              icon={
                <Image
                  size="mini"
                  style={{ width: 23, height: 23 }}
                  src="https://img.icons8.com/color/48/000000/person-male.png"
                />
              }
            ></BottomNavigationAction>
            <BottomNavigationAction
              // label="NearBy"
              value={"Nearby"}
              label="NearBy"
              icon={
                <Image
                  size="mini"
                  style={{ width: 23, height: 23 }}
                  src="https://img.icons8.com/color/96/000000/gps-device.png"
                />
              }
            ></BottomNavigationAction>
            <BottomNavigationAction
              value={"Ranking"}
              label="Ranking"
              icon={
                <Image
                  size="mini"
                  style={{ width: 23, height: 23 }}
                  src="https://img.icons8.com/color/48/000000/prize.png"
                />
              }
            ></BottomNavigationAction>
            <BottomNavigationAction
              // label="About"
              value={"About"}
              label="About"
              icon={
                <Image
                  size="mini"
                  style={{ width: 23, height: 23 }}
                  src="https://img.icons8.com/color/96/000000/about.png"
                />
              }
            ></BottomNavigationAction>
          </BottomNavigation>
        </ThemeProvider>
      ) : (
        ""
      )}
    </Visibility>
  );
};

const mapStateToProps = (state) => {
  return {
    currentTab: state.navigationReducer.currentTab,
  };
};

export default connect(mapStateToProps)(ForMobile);
