import React from "react";
import { Grid } from "@material-ui/core";
import { Responsive, Divider } from "semantic-ui-react";
const ForMob = (props) => {
  return (
    <Grid Container>
      <Grid item xs={10} style={{ display: "flex", justifyContent: "center" }}>
        {props.children}
      </Grid>
    </Grid>
  );
};

const ForTC = (props) => {
  return (
    <Grid container>
      <Grid item xs={3}></Grid>
      <Grid item xs={6} style={{ display: "flex", justifyContent: "center" }}>
        {props.children}
      </Grid>
      <Grid item xs={3}></Grid>
    </Grid>
  );
};

const Description = (props) => {
  return (
    <div>
      <div
        style={{
          fontFamily: "poppins",
          fontWeight: "bold",
          lineHeight: 2,
          fontSize: 20,
          padding: 20,
          marginBottom: 100,
          flexGrow: 3,
        }}
      >
        This Website Basically Works As a Complaint Management System For the
        College. you can post your complaints (either Anonymously or vice versa)
        and If your Complaint Manage To get Enough UpVotes Then That Problem
        Will Be Visible To Your Staff And They'll Take Action if they find that
        your complaint is Worth Considering(Remember DownVotes Will Also get
        Counted "-ve") . This Website Also Allow Staff To Post Notices. And Also
        There is a Separated Section for Notes Where Teachers Can Post Notes in
        an Organized Manner On The Website So That Any Student Can Access Them.
      </div>
      <div>
        <Divider
          horizontal
          style={{
            fontSize: 13,
            marginTop: "50%",
            fontFamily: "poppins",
            marginBottom: 100,
            textTransform: "none",
          }}
        >
          By @Paras_Kanwar
        </Divider>
      </div>
    </div>
  );
};

export const About = (props) => {
  return (
    <div>
      <Responsive {...Responsive.onlyComputer}>
        <ForTC>
          <Description></Description>
        </ForTC>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <ForTC>
          <Description></Description>
        </ForTC>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <Description></Description>
      </Responsive>
    </div>
  );
};
