import React from "react";
import { Avatar } from "@material-ui/core";
import { green, blue } from "@material-ui/core/colors";
import moment from "moment";
import "./chat.css";
const ChatSession = ({ setSession, sessions, session, MyId }) => {
  return (
    <React.Fragment>
      <div
        style={{ maxHeight: "70vh", overflow: "auto" }}
        className="thinscrollbar comingFromLeftP5"
      >
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginRight: 10,
            flexDirection: "column",
            alignItems: "center",
            overflow: "auto",
          }}
        >
          <RenderChatSession
            MyId={MyId}
            sessions={sessions}
            sessionId={session}
            onSelect={(sessionId) => {
              setSession(sessionId);
            }}
          ></RenderChatSession>
        </div>
      </div>
    </React.Fragment>
  );
};

const RenderChatSession = ({ sessions, onSelect, sessionId, MyId }) => {
  return (
    <React.Fragment>
      {sessions.map((session, index) => {
        return (
          <SingleSession
            onClick={(id) => {
              onSelect(id);
            }}
            key={index}
            firstName={session.personNextSide.firstName}
            active={session.session === sessionId}
            id={session.session}
            profilephoto={session ? session.personNextSide.profilephoto : ""}
            LastName={session.personNextSide.LastName}
            online={session.personNextSide.Online.status === "Online"}
            lastSeen={session.personNextSide.Online.lastSeen}
            UnReadMessageCount={session.UnReadMessageCount}
            MyId={MyId}
          ></SingleSession>
        );
      })}
    </React.Fragment>
  );
};

const SingleSession = ({
  id,
  profilephoto,
  firstName,
  LastName,
  onClick,
  active,
  online,
  UnReadMessageCount,
  lastSeen,
  MyId,
}) => {
  return (
    <div
      style={{
        width: "100%",
        color: "black",
        cursor: "pointer",
        padding: 10,
        height: "100px",
        background: active ? "rgba(0,0,0,0.05)" : "rgba(0,0,0,0.02)",
        display: "flex",
        transition: "0.5s ease",
        alignItems: "center",
      }}
      onClick={() => onClick(id)}
    >
      <div style={{ position: "relative", marginLeft: "10%", marginRight: 10 }}>
        <Avatar src={profilephoto}></Avatar>

        <div
          style={{
            position: "absolute",
            bottom: 0,
            left: 0,
            height: 10,
            width: 10,
            borderRadius: "50%",
            backgroundColor: online ? green[700] : "transparent",
            transform: online
              ? "translateY(-40px) translateX(25px)"
              : "translateY(0px) translateX(0px)",
            transition: "0.5s ease",
          }}
        ></div>
      </div>
      <div
        style={{
          fontFamily: "poppins",
          fontWeight: "bold",
        }}
      >
        <div>{firstName + " " + LastName}</div>
        {lastSeen ? <div style={{ fontSize: 10 }}>Last Seen</div> : ""}
        <div style={{ fontWeight: "lighter", fontSize: 12 }}>
          {lastSeen
            ? moment(lastSeen).format("MMMM Do YYYY, h:mm:ss a")
            : online
            ? "Online"
            : ""}
        </div>
      </div>
      {UnReadMessageCount.length &&
      UnReadMessageCount.find(({ forParticipant }) => {
        return forParticipant === MyId;
      }).count !== 0 ? (
        <div
          className={
            UnReadMessageCount.find(({ forParticipant }) => {
              return forParticipant === MyId;
            }).count %
              2 ===
            0
              ? "animationOnEven"
              : "animationOnOdd"
          }
          style={{
            height: 30,
            width: 30,
            background: blue[700],
            borderRadius: "50%",
            color: "white",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            marginLeft: "auto",
            zIndex: 100,
          }}
        >
          <div style={{ fontSize: 14 }}>
            {
              UnReadMessageCount.find(({ forParticipant }) => {
                return forParticipant === MyId;
              }).count
            }
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default ChatSession;
