import React, { useState, Fragment } from "react";
import { connect } from "react-redux";
import SearchNotes from "./SearchNotes/SearchNotes";
import { CenterStuff } from "../Person/OthersProfile/profile/Profile";
import { Divider } from "semantic-ui-react";
import { Fab, Snackbar } from "@material-ui/core";
import AddNotes from "./AddNote/AddNote";
import Subject from "./Subjects/Subject/Subject";
import { ISMISM } from "./Subjects/insideSubjectModal/ISMISM/ISMISM";

const Notes = (props) => {
  const [addNotes, setAddNotes] = useState(false);
  const [successPosting, setSuccess] = useState(false);
  const [pageState, setPage] = useState("unknown");
  const [subject, setSubject] = useState(null);
  const [topic, setTopic] = useState(null);

  return (
    <Fragment>
      <Snackbar
        autoHideDuration={2000}
        onClose={() => {
          setSuccess(false);
        }}
        transitionDuration={500}
        open={successPosting}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        message="Notes Posted...."
      ></Snackbar>

      <CenterStuff style={{ marginBottom: 30 }}>
        <SearchNotes
          handleSubjectChange={(subject) => {
            setPage("subject");
            setSubject(subject);
          }}
          handleTopicChange={(topic) => {
            setPage("topic");
            setTopic(topic);
          }}
        ></SearchNotes>
      </CenterStuff>
      {pageState === "subject" ? <Subject subject={subject}></Subject> : ""}
      <Fragment>
        {addNotes ? (
          <AddNotes
            open={addNotes}
            onClose={() => {
              setAddNotes(false);
            }}
            onSuccess={() => setSuccess(true)}
          ></AddNotes>
        ) : (
          ""
        )}
      </Fragment>
      {pageState === "unknown" ? <Divider horizontal>Or</Divider> : ""}

      <CenterStuff style={{ transition: "1s ease", ...getStyle(pageState) }}>
        {pageState === "unknown" ? (
          <div
            style={{ margin: 15, fontFamily: "poppins", fontWeight: "bold" }}
          >
            Add Notes...
          </div>
        ) : (
          ""
        )}
        <Fab
          style={{
            marginLeft: 25,
            background: "white",
            boxShadow: "0px 0px 20px rgba(0,0,0,0.2)",
          }}
          onClick={() => {
            setAddNotes(true);
          }}
        >
          <img
            style={{ width: 30, height: 30 }}
            src="https://img.icons8.com/color/50/000000/plus-math.png"
          />
        </Fab>
      </CenterStuff>
      {topic ? (
        <ISMISM
          onClose={() => {
            setTopic(null);
            setPage("unknown");
          }}
          noteDetails={topic}
          isUpVoted={topic.upVotedBy.find(
            (user) => user.user.toString() === props.id
          )}
          isDownVoted={topic.downVotedBy.find(
            (user) => user.user.toString() === props.id
          )}
        ></ISMISM>
      ) : (
        ""
      )}
    </Fragment>
  );
};

const getStyle = (pageState) => {
  return pageState === "unknown"
    ? {
        left: "auto",
        bottom: "auto",
      }
    : {
        position: "fixed",
        right: "3%",
        bottom: "12%",
      };
};
let mapStateToProps = (state) => {
  return {
    id: state.userReducer.id,
  };
};
const Notes1 = connect(mapStateToProps)(Notes);

export { Notes1 as Notes };
